import Vue from 'vue'
import Router from 'vue-router'
import List from '@/components/list/list'
import SList from '@/components/list/sList'
import newBank from '@/components/list/newBank'
import pageTeacher from '@/page/teacher/index'
import PageStudent from '@/page/student/index'
import reply from '@/components/list/reply'
Vue.use(Router)

export default new Router({
  mode: 'history',
  base: '/dist/',
  routes: [
    {
      path: '/t',
      name: 'pageTeacher',
      component: pageTeacher,
      children: [{
      	path: '/',
      	name: "List",
      	component: List
      },{
      	path: '/n',
      	name: "newBank",
      	component: newBank
      }]
    },
    {
      path: '/',
      name: 'PageStudent',
      component: PageStudent,
      children: [{
        path: '/',
        name: "SList",
        component: SList
      },{
        path: '/r',
        name: "reply",
        component: reply
      }]
    }
  ]
})



