<template>
	<div class="container">
		<div class="newBankContainer">
			<div class="questionList" v-for="(item, index) in questionList">
				<div class="issue">{{index+1}}. {{item.issue}}</div>
				<div class="selector">
					<ul>
						<li v-for="(selector, i) in item.selector">
							<input type="radio" :name="index" /><span>{{selector.val}}</span>
						</li>
					</ul>
				</div>
			</div>
			<div class="operation">
				<Button type="success" title="保存答案"></Button>
				<Button type="warning" title="提交答案"></Button>
			</div>
		</div>
	</div>
</template>
<script type="text/javascript">
import Button from '@/components/button'
	export default {
		name: "Reply",
		data() {
			return {
				questionList: [{
					issue: '下列关于静电场的说法中正确的是（）',
					selector: [{
						isRight: 0,
						val: '在点电荷形成的电场中没有场强相等的两点，但有电势相等的两点'
					},{
						isRight: 0,
						val: '正电荷只在电场力作用下，一定从高电势向低电势运动'
					},{
						isRight: 0,
						val: '场强为零处，电势不一定为零；电势为零处，场强不一定为零'
					},{
						isRight: 0,
						val: '初速为零的正电荷在电场力作用下不一定沿电场线运动'
					}]
				},{
					issue: '在总电压不变的条件下，黄昏时电灯比深夜暗，是因为黄昏时()',
					selector: [{
						isRight: 0,
						val: '线路中总电阻变大，电流变小'
					},{
						isRight: 0,
						val: '总电流一定，支路增多分去了电流'
					},{
						isRight: 0,
						val: '干线电流过大，线路损失电压增大'
					},{
						isRight: 0,
						val: '总电阻变小，每支路电流增大'
					}]
				},{
					issue: '灯泡中灯丝断了，将灯丝搭接起来再用，则以下判断正确的是()',
					selector: [{
						isRight: 0,
						val: '比原来更亮'
					},{
						isRight: 0,
						val: '比原来更暗 '
					},{
						isRight: 0,
						val: '搭接起来的地方容易烧断，其他地方不易断'
					},{
						isRight: 0,
						val: '搭接起来的地方不容易断，其他地方容易烧断 '
					}]
				},{
					issue: '要减小发射电磁波的周期，下列哪些办法是正确的()',
					selector: [{
						isRight: 0,
						val: '减小线圈的匝数'
					},{
						isRight: 0,
						val: '抽去线圈中的磁芯'
					},{
						isRight: 0,
						val: '降低电容器的充电电压'
					},{
						isRight: 0,
						val: '减小电容器的电容 '
					}]
				},{
					issue: '在赤道附近有一竖直向下的匀强电场，在此区域内有一根沿东西方向放置的直导体棒，由水平位置自静止落下，不计空气阻力，则导体棒两端落地的先后关系是()',
					selector: [{
						isRight: 0,
						val: '东端先落地'
					},{
						isRight: 0,
						val: '西端先落地'
					},{
						isRight: 0,
						val: '两端同时落地'
					},{
						isRight: 0,
						val: '无法确定'
					}]
				},{
					issue: '静电场中有Ｍ、Ｎ两点，已知Ｍ点的电势高于Ｎ点的电势，则下面的说法正确的是()',
					selector: [{
						isRight: 0,
						val: 'Ｍ点到Ｎ点连线的方向就是电场线的方向'
					},{
						isRight: 0,
						val: '一个点电荷到Ｍ点受的电场力总要大于在Ｎ点受的电场力'
					},{
						isRight: 0,
						val: '一个点电荷从Ｎ点移到Ｍ点，它的电势能一定增加'
					},{
						isRight: 0,
						val: '一个点电荷从Ｍ点移到Ｎ点，它的电势能可能增加'
					}]
				}]
			}
		},
		components: {
			Button
		},
		methods: {
		}
	}
</script>
<style type="text/css">
.container {
	width: 100%;
}
.mt {
	margin-top: 20px;
}
.newBankContainer {
	width: 940px;
	padding: 30px;
	margin: 0 auto;
	background: white;
	box-shadow: 0 0 1rem #aaa;
}
.questions {
	width: 100%;
}
.questionType {
	width: 100%;
	margin: 15px 0;
	transition: height .4s; 
	overflow: hidden;
}
.addBank {
	width: 100%;
	height: 60px;
	margin-top: 15px;
	cursor: pointer;
	line-height: 60px;
	background: #ebf7ff;
	border: 1px solid #3af7bb;
}
.operation {
	width: 100%;
	margin: 15px 0;
	height: 30px;
}
.questionList {
	width: 96%;
	padding: 15px 2%;
	margin: 20px 0;
	background: #ccc;
	text-align: left;
}
.selector ul {
	  list-style: none;
}
</style>


